/* src/index.css */

@tailwind base;
@tailwind components;
@tailwind utilities;

/* Center the login form */
.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
    color:#45a049;
    background-color:black;
  }
  .login-box {
    background-color: green;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 0 15px rgba(255, 255, 255, 0.1);
  }
  
  
  .login-title {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .password-input {
    width: 200px;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .login-btn {
    padding: 10px 20px;
    border: none;
    background-color: #4CAF50;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .login-btn:hover {
    background-color: #45a049;
  }
  
  /* Notes container */
  .notes-container {
    padding: 20px;
    text-align: center;
  }
  
  /* Save note input */
  .note-input-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
    background-color:black;
  }
  
  .note-input {
    width: 250px;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .save-note-btn {
    padding: 10px 20px;
    background-color: #008CBA;
    color: white;
    font-size: 1rem;
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .save-note-btn:hover {
    background-color: #007b8c;
  }
  
  /* Saved notes section */
  .saved-notes-container {
    margin-top: 20px;
  }
  
  .note-item {
    margin-bottom: 10px;
  }
  
  .decrypt-btn {
    padding: 5px 10px;
    background-color: #f1c40f;
    color: white;
    cursor: pointer;
    border-radius: 5px;
    border: none;
  }
  
  .decrypt-btn:hover {
    background-color: #f39c12;
  }
  
  /* Delete button */
  .delete-all-btn {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #e74c3c;
    color: white;
    font-size: 1rem;
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .delete-all-btn:hover {
    background-color: #c0392b;
  }
  
  /* Decrypted note display */
  .decrypted-note-container {
    margin-top: 20px;
    font-size: 1.2rem;
    background-color: #ecf0f1;
    padding: 10px;
    border-radius: 5px;
  }
  